.labels-container {
    position: fixed;
    border: 1px solid #d2d2d2;
    z-index: 999;
    width: 300px;
    padding: 10px;
    border-radius: 3px;
    background-color: white;
    top: 40px;
    right: 40px;
    .labels-header {
        padding: 4px;
        border-bottom: 1px solid #d5d5d5;
        margin-bottom: 10px;
        text-align: center;
    }
    .label-item {
        padding: 8px 12px;
        margin: 5px;
        border-radius: 5px;
        color: white;
        height: 35px;
        cursor: pointer;
        font-size: rem(14px);
        font-family: openSansBold;
        transition: .3s;

        &:hover {
            filter: brightness(90%);
        }

        & img {
            width: 16px;
        }
    }
    .close-label {
        background: none;
        border: none;
        border-radius: 50%;
        padding: 10px;
        color: #888888;
        transition: 0.3s;
        &:hover {
            background-color: #eeeeee;
        }
    }
}
